export default {
  name: "",
  data: function data() {
    return {
      winningNum: {
        special: ["41482012"],
        grand: ["58837976"],
        first: ["20379435", "47430762", "36193504"],
        additional: ["693", "043", "425"]
      },
      manualInput: "",
      result: "",
      radios: "manual",
      sounds: {
        notGet: "/notGet.mp3",
        getMomey: "/coin.mp3"
      }
    };
  },
  components: {},
  created: function created() {},
  mounted: function mounted() {// this.msg("恭喜中獎", "success");
    // this.msg("沒有中獎","error")
  },
  watch: {
    manualInput: function manualInput() {
      // 手動兌獎時，超過3個數字，不兌獎
      if (this.manualInput.length > 3) return;
      console.log(this.manualInput, this.result);
      this.checkWinning("manual", this.manualInput);
    },
    result: function result() {
      if (this.result !== "") {
        if (this.result.includes("悲劇")) {
          console.log(this.result); // this.showAlert(this.result, "notGet");

          this.msg(this.result, "notGet");
        } else {
          console.log(this.result);
          this.msg(this.result, "getMomey"); // this.showAlert(this.result, "getMomey");
        }

        this.result = ""; //reset result
      }
    }
  },
  computed: {},
  methods: {
    msg: function msg(_msg) {
      var audioItem = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : "getMomey";
      var tpe = audioItem === "notGet" ? "error" : "success";
      var audio = new Audio(this.sounds[audioItem]);
      audio.play();
      this.$message({
        showClose: true,
        message: _msg,
        type: tpe
      });
    },
    checkWinning: function () {
      //這個chenkIncloude function 
      //是給下面 return 中的 function程式碼呼叫的
      //最後面加了()立即執行，以callback方式執行
      //先兌獎，1秒後清空manualInput
      function checkIncloude(self, winning, number, pattern) {
        return self.winningNum[winning].map(function (item) {
          return item.match(pattern)[0];
        }).includes(number.match(pattern)[0]);
      }

      return function (mode, number) {
        var _this = this;

        if (mode === "QRMode") {
          if (checkIncloude(this, "special", number, /\d{8}$/)) {
            this.result = "".concat(number, " \u4E2D\u7279\u5225\u734E");
          } else if (checkIncloude(this, "grand", number, /\d{8}$/)) {
            this.result = "".concat(number, " \u4E2D\u7279\u734E");
          } else if (checkIncloude(this, "first", number, /\d{3}$/)) {
            this.result = "".concat(number, " \u4E2D\u516D\u734E");

            if (checkIncloude(this, "first", number, /\d{4}$/)) {
              this.result = "".concat(number, " \u4E2D\u4E94\u734E");

              if (checkIncloude(this, "first", number, /\d{5}$/)) {
                this.result = "".concat(number, " \u4E2D\u56DB\u734E");

                if (checkIncloude(this, "first", number, /\d{6}$/)) {
                  this.result = "".concat(number, " \u4E2D\u4E09\u734E");

                  if (checkIncloude(this, "first", number, /\d{7}$/)) {
                    this.result = "".concat(number, " \u4E2D\u4E8C\u734E");

                    if (checkIncloude(this, "first", number, /\d{8}$/)) {
                      this.result = "".concat(number, " \u4E2D\u982D\u734E");
                    }
                  }
                }
              }
            }
          } else if (checkIncloude(this, "additional", number, /\d{3}$/)) {
            this.result = "".concat(number, " \u4E2D\u589E\u958B\u516D\u734E");
          } else {
            this.result = "".concat(number, " \u60B2\u5287 \u4EC0\u9EBC\u90FD\u6C92\u4E2D");
          }
        } else if (mode === "manual") {
          if (number.length < 3) return false;

          if (checkIncloude(this, "special", number, /\d{3}$/)) {
            this.result = "注意特別獎";
          } else if (checkIncloude(this, "grand", number, /\d{3}$/)) {
            this.result = "注意特獎";
          } else if (checkIncloude(this, "first", number, /\d{3}$/)) {
            this.result = "中兩百注意頭獎";
          } else if (checkIncloude(this, "additional", number, /\d{3}$/)) {
            this.result = "中增開六獎";
          } else {
            this.result = "悲劇 什麼都沒中";
          }

          window.setTimeout(function () {
            _this.manualInput = "";
          }, 1000);
        }
      };
    }() //輸入三個數字後馬上執行

  }
};